code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.slow-spin {
  -webkit-animation: fa-spin 6s infinite linear;
  animation: fa-spin 6s infinite linear;
}
.navbar-brand{
  font-size: 45px !important;
  font-weight: 600;
}
.myLink{
  color :#FFF !important;
  font-size: 20px;
  font-weight: 500;
}
.active{
  color: coral !important;
}
.homeSection{
  background-color: rgb(26, 188, 156);
}

.starIcon {
  position: relative;
  font-size: 40px;
  padding: 20px;
  text-align: center;
  
}
.starIcon i{
  font-size: 40px;
  color: white;
}
.starIcon::after{
  content: "";
  position: absolute;
  left: 100%;
  top: 50%;
  background-color: white;
  width: 100px;
  height: 5px;
}

.starIcon::before{
  content: "";
  position: absolute;
  right: 100%;
  top: 50%;
  background-color: white;
  width: 100px;
  height: 5px;
}
.pageHeader{
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
}

.ko7lyColor{
  color: rgb(44, 62, 80) !important;
}
.background-ko7ly{
  background-color: rgb(44, 62, 80) !important ;
}
.bg-ko7ly::before,.bg-ko7ly::after{
  background-color: rgb(44, 62, 80) !important ;
}

.greenColor{
  color: rgb(26, 188, 156) !important;
}
.bg-green
{
  background-color: rgb(26, 188, 156) !important;
}




