
.lightBox{
    background-color: rgba(0, 0,0,0.6);
    padding: 40px 100px;
    display: none;
    z-index: 55555;
    overflow: auto;
  }
  
  #lightBox .container{
    border:5px solid rgb(26, 188, 156);;
    border-radius: 20px;
    /* background-color: blue !important; */
  }
  .btn{
    border-color: rgb(26, 188, 156);
    transition: background-color 0.5s ;
  }
  .btn:hover{
    background-color: rgb(4, 17, 14) !important;
    border-color: rgb(20, 143, 119) !important;
  }
  
  #closeIcon{
    font-size: 60px;
  position: absolute;
  top: 5px;
  right: 40px;
  
  }
  
  #closeIcon i:hover {
  cursor: pointer;
  }
  
  .lightBoxHeader{
    font-size: 70px;
}
 
  
@media screen and (max-width:576px) {
   
    #closeIcon{
        font-size: 30px;
        right: 20px;
      }
  .lightBoxHeader{
      font-size: 36px;
  }
    }
  
  @media screen and (max-width:768px) and (min-width:577px)  {
   
    #closeIcon{
        font-size: 40px;
        right: 20px;    
      }
      .lightBoxHeader{
          font-size: 40px;
      }
  }

  @media screen and (max-width:992px) and (min-width:769px)  {
    #closeIcon{
        font-size: 50px;
        right: 20px;    
      }
      .lightBoxHeader{
          font-size: 50px;
      }
  }